<template>
  <div>
    <h2>INBOX</h2>
    <div v-for="box in inbox" :key="box.doc.title">
      <p>{{ box.doc.title }}</p>
      <input v-model="comment" placeholder="Comment" />
      <button v-on:click="approve(box.doc.id)">Approve</button>
      <button v-on:click="deny(box.doc.id)">Deny</button>
    </div>
  </div>
</template>

<script>
import axios from "axios"

export default {
  created() {
    this.getInbox()
  },
  data() {
    return {
      inbox: [],
      comment: "",
    }
  },
  methods: {
    getInbox() {
      axios.get("/api/doc/inbox").then((res) => {
        console.log(res.data.inbox)
        this.inbox = res.data.inbox
      })
    },
    approve(id) {
      axios
        .post("/api/doc/sign", {
          docId: id,
          decision: "APPROVED",
          comment: this.comment.length == 0 ? null : this.comment,
        })
        .then((res) => console.log(res))
        .catch((e) => console.error(e))
    },
    deny(id) {
      axios
        .post("/api/doc/sign", {
          docId: id,
          decision: "DENIED",
          comment: this.comment.length == 0 ? null : this.comment,
        })
        .then((res) => console.log(res))
        .catch((e) => console.error(e))
    },
  },
}
</script>

