<template>
  <div>
    <h2>ARCHIVE</h2>
    <div v-for="box in archive" :key="box.title">
      <p>{{ box.title }}</p>
    </div>
  </div>
</template>

<script>
import axios from "axios"
export default {
  created() {
    this.getArchive()
  },
  data() {
    return {
      archive: [],
    }
  },
  methods: {
    getArchive() {
      axios.get("/api/doc/archive").then((res) => {
        console.log(res.data.outbox)
        this.archive = res.data.outbox
      })
    },
  },
}
</script>

