<template>
  <div>
    <input v-model="id" placeholder="id" />
    <input v-model="pw" placeholder="pw" />
    <button v-on:click="login">Login</button>
    <br />
    <br />

    <input v-model="idj" placeholder="idjoin" />
    <input v-model="pwj" placeholder="pwjoin" />
    <button v-on:click="join">Join</button>
    <br />
    <br />

    <button v-on:click="logout">Logout</button>
    <br />
    <br />
  </div>
</template>

<script>
import axios from "axios"

export default {
  data() {
    return {
      id: "",
      pw: "",
      idj: "",
      pwj: "",
    }
  },
  methods: {
    login() {
      const param = {
        id: this.id,
        password: this.pw,
      }
      console.log("login request")
      axios
        .post("/auth/login", param)
        .then(() => console.log("eeeeeee"))
        .catch((e) => console.log(e))
    },
    join() {
      const param = {
        id: this.idj,
        password: this.pwj,
      }
      console.log("join request")
      axios
        .post("/auth/join", param)
        .then(() => console.log("eeeeeee"))
        .catch((e) => console.log(e))
    },
    logout() {
      axios
        .post("/api/logout")
        .then(() => console.log("eeeeeee"))
        .catch((e) => console.log(e))
    },
  },
}
</script>

