<template>
  <div>
    <h2>결재 문서 생성</h2>
    <div
      style="display:flex; flex-direction: row; justify-content: space-evenly;"
    >
      <div>
        <h2>문서 내용</h2>
        <input v-model="title" placeholder="제목" />
        <br />
        <textarea v-model="content" placeholder="내용"></textarea>
      </div>
      <div>
        <h2>문서 분류</h2>
        <div v-for="cat in cats" :key="cat.name">
          <input type="radio" :id="cat.id" :value="cat.id" v-model="pkCat" />
          <label :for="cat">{{ cat.name }}</label>
        </div>
      </div>
      <div>
        <h2>결재자</h2>
        <div v-for="user in users" :key="user.name">
          <input
            type="checkbox"
            :id="user.name"
            :value="user.name"
            v-model="pkUsers"
          />
          <label :for="user.name">{{ user.name }}</label>
          <br />
        </div>
        <span>체크한 이름 (순서대로): {{ pkUsers }}</span>
      </div>
    </div>

    <button v-on:click="save">저장</button>
  </div>
</template>

<script>
import axios from "axios"

export default {
  created() {
    this.getCat()
    this.getUsers()
  },
  data() {
    return {
      cats: [],
      users: [],
      pkCat: -1,
      pkUsers: [],
      title: "",
      content: "",
    }
  },
  methods: {
    getCat() {
      axios.get("/api/category/all").then((res) => {
        this.cats = res.data
      })
    },
    getUsers() {
      axios.get("/api/user/page").then((res) => {
        this.users = res.data.users
      })
    },
    save() {
      const param = {
        title: this.title,
        category: this.pkCat,
        content: this.content,
        users: this.pkUsers,
      }
      console.log(param)
      axios.post("/api/doc", param).then((res) => {
        console.log(res)
      })
    },
  },
}
</script>

