<template>
  <div>
    <h2>OUTBOX</h2>
    <div v-for="box in outbox" :key="box.title">
      <p>{{ box.title }}</p>
    </div>
  </div>
</template>

<script>
import axios from "axios"

export default {
  created() {
    this.getOutbox()
  },
  data() {
    return {
      outbox: [],
    }
  },
  methods: {
    getOutbox() {
      axios.get("/api/doc/outbox").then((res) => {
        console.log(res.data.outbox)
        this.outbox = res.data.outbox
      })
    },
  },
}
</script>

